import React from 'react';
import styles from './styles.module.scss';
function List({title, list}) {
  return (
    <div className={styles['cert-list']}>
      <h1>{title}</h1>
      <ul>
        {list.map(item => (
          <li key={item.id}>
            <div className={styles['img-box']}>
              <img src={item.img} alt=""/>
            </div>
            <p>{item.tit}</p>
            <p>{item.desc}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
export default List;
